<template>
  <div>
    <!---->
    <bread :fromComponentbread="dataPath"></bread>
    <!---->
    <el-table :data="rightsList" stripe style="width: 100%">
      <el-table-column type="index" label=""> </el-table-column>
      <el-table-column prop="authName" label=""> </el-table-column>
      <el-table-column prop="path" label=""> </el-table-column>
      <el-table-column prop="level" label="">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.level == 0"></el-tag>
          <el-tag type="success" v-else-if="scope.row.level == 1">
            
          </el-tag>
          <el-tag type="warning" v-else></el-tag>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import bread from "@/components/bread";
export default {
  components: {
    bread
  },
  data() {
    return {
      dataPath: [{ name: "" }, { name: "" }],
      rightsList: []
    };
  },
  created() {
    this.getrightsList();
  },
  methods: {
    async getrightsList() {
      const res = await this.$http.get("rights/list");
      if (res.data.meta.status !== 200) {
        return this.$message.error("");
      } else {
        this.rightsList = res.data.data;
        console.log(this.rightsList);
      }
    }
  }
};
</script>

<style lang="less" scoped>
.el-table {
  margin-top: 20px;
}
</style>



