<template>
  <div>
    <!---->
    <bread :fromComponentbread="dataPath"></bread>
    <!---->
    <el-card class="box-card">
      <el-button type="danger" round @click="isroledialog = true">
        
      </el-button>
      <!---->
      <el-table :data="rolesList" style="width: 100%" stripe>
        <el-table-column type="expand" label="" width="100px">
          <template slot-scope="scope">
            <el-row
              :class="['bdbottom', index1 === 0 ? 'bdtop' : '']"
              v-for="(item1, index1) in scope.row.children"
              :key="index1"
            >
              <!---->
              <el-col :span="5">
                <el-tag closable @close="deleterights(scope.row, item1.id)">{{
                  item1.authName
                }}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!---->
              <el-col :span="19">
                <!---->
                <el-row
                  v-for="(item2, index2) in item1.children"
                  :key="index2"
                  :class="[index2 === 0 ? '' : 'bdtop']"
                >
                  <el-col :span="6">
                    <el-tag
                      closable
                      type="success"
                      @close="deleterights(scope.row, item2.id)"
                    >
                      {{ item1.authName }}
                    </el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <!---->
                  <el-col :span="18">
                    <el-tag
                      closable
                      type="warning"
                      v-for="(item3, index3) in item2.children"
                      :key="index3"
                      @close="deleterights(scope.row, item3.id)"
                    >
                      {{ item3.authName }}
                    </el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column type="index" label=""> </el-table-column>
        <el-table-column prop="roleName" label=""> </el-table-column>
        <el-table-column prop="roleDesc" label=""> </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-tooltip
              effect="light"
              content=""
              placement="top-start"
              :enterable="false"
            >
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="showRoledialog(scope.row.id)"
              >
              </el-button>
            </el-tooltip>
            <el-tooltip
              effect="light"
              content=""
              placement="top-start"
              :enterable="false"
            >
              <el-button
                type="warning"
                icon="el-icon-delete"
                size="mini"
                @click="showDeletetip(scope.row.id)"
              >
              </el-button>
            </el-tooltip>
            <el-tooltip
              effect="light"
              content=""
              placement="top-start"
              :enterable="false"
            >
              <el-button
                type="danger"
                icon="el-icon-setting"
                size="mini"
                @click="showRights(scope.row)"
              >
              </el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!---->
      <el-dialog
        title=""
        :visible.sync="isroledialog"
        width="50%"
        center
        @close="roleDialogClose()"
      >
        <span>
          <el-form
            ref="roleformRef"
            :model="roleform"
            label-width="80px"
            :rules="rolerules"
          >
            <el-form-item label="" prop="roleName">
              <el-input v-model="roleform.roleName"></el-input>
            </el-form-item>
            <el-form-item label="" prop="roleDesc">
              <el-input v-model="roleform.roleDesc"></el-input>
            </el-form-item>
          </el-form>
        </span>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="addrole()"> </el-button>
          <el-button @click="isroledialog = false">
             
          </el-button>
        </span>
      </el-dialog>
      <!---->
      <el-dialog
        title=""
        :visible.sync="isEditRoledialog"
        width="50%"
        center
        @close="EditroleDialogClose()"
      >
        <span>
          <el-form
            ref="EditroleformRef"
            :model="Editroleform"
            label-width="80px"
            :rules="rolerules"
          >
            <el-form-item label="" prop="roleName">
              <el-input v-model="Editroleform.roleName"></el-input>
            </el-form-item>
            <el-form-item label="" prop="roleDesc">
              <el-input v-model="Editroleform.roleDesc"></el-input>
            </el-form-item>
          </el-form>
        </span>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="Editrole()"> </el-button>
          <el-button @click="isEditRoledialog = false">
             
          </el-button>
        </span>
      </el-dialog>
      <!---->
      <el-dialog
        title=""
        :visible.sync="showrightdialog"
        width="50%"
        @close="showrightdialogclose()"
      >
        <el-tree
          :data="rightList"
          :props="treeprop"
          show-checkbox
          node-key="id"
          default-expand-all
          :default-checked-keys="treekey"
          ref="treeRef"
        ></el-tree>
        <span slot="footer" class="dialog-footer">
          <el-button @click="showrightdialog = false"> </el-button>
          <el-button type="primary" @click="addrights()">  </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import bread from "@/components/bread";
export default {
  components: {
    bread
  },
  data() {
    return {
      dataPath: [{ name: "" }, { name: "" }],
      //
      rolesList: [],
      //
      isroledialog: false,
      //
      isEditRoledialog: false,
      //
      roleform: {
        roleName: "",
        roleDesc: ""
      },
      //
      Editroleform: {},
      //
      rolerules: {
        roleName: [
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 10, message: " 2  6 ", trigger: "blur" }
        ],
        roleDesc: [
          { required: true, message: "", trigger: "blur" },
          { min: 5, max: 10, message: " 5  10 ", trigger: "blur" }
        ]
      },
      //
      rightList: [],
      //
      showrightdialog: false,
      //
      treeprop: {
        label: "authName",
        children: "children"
      },
      //id
      treekey: [],
      //id
      roleid: ""
    };
  },
  created() {
    this.getrolesList();
  },
  methods: {
    //
    async getrolesList() {
      const res = await this.$http.get("roles");
      if (res.data.meta.status !== 200) {
        return $this.message.erroe("");
      } else {
        this.rolesList = res.data.data;
        console.log(this.rolesList);
      }
    },
    //
    async showRoledialog(id) {
      this.isEditRoledialog = true;
      const res = await this.$http.get(`roles/${id}`);
      console.log(res);
      this.Editroleform = res.data.data;
    },
    //
    roleDialogClose() {
      this.$refs.roleformRef.resetFields();
    },
    //
    EditroleDialogClose() {
      this.$refs.EditroleformRef.resetFields();
    },
    showrightdialogclose() {
      this.treekey = [];
    },
    //
    addrole() {
      this.$refs.roleformRef.validate(async valid => {
        if (!valid) {
          return this.$message.error("");
        } else {
          const res = await this.$http.post("roles", this.roleform);
          console.log(res);
          if (res.data.meta.status !== 201) {
            return this.$message.error("");
          } else {
            this.$message.success("");
            this.isroledialog = false;
            this.getrolesList();
          }
        }
      });
    },
    //
    Editrole() {
      this.$refs.EditroleformRef.validate(async valid => {
        if (!valid) {
          return this.$message.error("");
        } else {
          const res = await this.$http.put(
            `roles/${this.Editroleform.roleId}`,
            this.Editroleform
          );
          if (res.data.meta.status !== 200) {
            return this.$message.error("");
          } else {
            this.$message.success("");
            this.isEditRoledialog = false;
            this.getrolesList();
          }
        }
      });
    },
    //
    async showDeletetip(id) {
      const confirmresult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).catch(err => err);
      if (confirmresult == "confirm") {
        const res = await this.$http.delete(`roles/${id}`);
        if (res.data.meta.status !== 200) {
          this.$message.error("");
        } else {
          this.$message.success("");
          this.getrolesList();
        }
      } else {
        return;
      }
    },
    //
    async deleterights(roleId, rightsId) {
      const confirmresult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).catch(err => err);
      if (confirmresult !== "confirm") {
        return;
      } else {
        const res = await this.$http.delete(
          `roles/${roleId.id}/rights/${rightsId}`
        );
        if (res.data.meta.status !== 200) {
          this.$message.error("");
        } else {
          this.$message.success("");
          roleId.children = res.data.data;
        }
      }
    },
    //
    async showRights(role) {
      this.roleid = role.id;
      const { data: res } = await this.$http.get("rights/tree");
      this.rightList = res.data;
      this.getrights(role, this.treekey);
      this.showrightdialog = true;
    },
    //
    async addrights() {
      const key = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ];
      const idstr = key.join(",");
      const { data: res } = await this.$http.post(
        `roles/${this.roleid}/rights`,
        {
          rids: idstr
        }
      );
      if (res.meta.status !== 200) {
        return this.$message.error("");
      } else {
        this.$message.success("");
        this.showrightdialog = false;
        this.getrolesList();
      }
    },
    //
    getrights(node, arr) {
      if (!node.children) {
        return arr.push(node.id);
      } else {
        node.children.forEach(item => this.getrights(item, arr));
      }
    }
  }
};
</script>

<style lang="less" scoped>
.el-card {
  margin-top: 20px;
}
.el-table {
  margin-top: 20px;
}
.el-row {
  display: flex;
  align-items: center;
}
.el-tag {
  margin: 15px 10px;
}
.bdtop {
  border-top: 1px solid #eee;
}
.bdbottom {
  border-bottom: 1px solid #eee;
}
</style>



