<template>
  <div>
    <!--  -->
    <bread :fromComponentbread="dataPath"></bread>
    <!--  -->
    <el-card>
      <!-- 2.EchartsDom -->
      <div id="main" style="width: 750px;height:400px;"></div>
    </el-card>
  </div>
</template>

<script>
// 1.echarts
import echarts from "echarts";
import _ from "lodash";
import bread from "@/components/bread";
export default {
  components: {
    bread
  },
  data() {
    return {
      dataPath: [{ name: "" }, { name: "" }],
      // 
      options: {
        title: {
          text: ""
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#E9EEF3"
            }
          }
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: [
          {
            boundaryGap: false
          }
        ],
        yAxis: [
          {
            type: "value"
          }
        ]
      }
    };
  },
  created() {},
  // ,,
  async mounted() {
    // 3.domecharts
    var myChart = echarts.init(document.getElementById("main"));
    const { data: res } = await this.$http.get("reports/type/1");
    if (res.meta.status !== 200) return this.$message("!");
    // 4.
    // 
    // var option = {
    //   title: {
    //     text: 'ECharts '
    //   },
    //   tooltip: {},
    //   legend: {
    //     data: ['']
    //   },
    //   xAxis: {
    //     data: ['', '', '', '', '', '']
    //   },
    //   yAxis: {},
    //   series: [{
    //     name: '',
    //     type: 'bar',
    //     data: [5, 20, 36, 10, 10, 20]
    //   }]
    // }
    // 
    const result = _.merge(res.data, this.options);
    // 5.
    myChart.setOption(result);
  },
  methods: {}
};
</script>

<style lang="less" scoped></style>



