<template>
  <div>
    <!--  -->
    <bread :fromComponentbread="dataPath"></bread>
    <!--  -->
    <el-card>
      <!--  -->
      <el-alert
        title=""
        type="info"
        center
        show-icon
        :closable="false"
      ></el-alert>
      <!--  -->
      <el-steps
        :space="200"
        :active="activeIndex - 0"
        finish-status="success"
        align-center
      >
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>

      <!-- Tab -->
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="100px"
        label-position="top"
      >
        <el-tabs
          v-model="activeIndex"
          :tab-position="'left'"
          :before-leave="beforeTabLeave"
          @tab-click="tabClicked"
        >
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="price">
              <el-input v-model="addForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input v-model="addForm.goods_weight" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input v-model="addForm.goods_number" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_cat">
              <el-cascader
                v-model="addForm.goods_cat"
                :options="cateList"
                :props="cascaderProps"
                @change="handleChange"
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <!-- Item -->
            <el-form-item
              v-for="item in manyTableData"
              :key="item.attr_id"
              :label="item.attr_name"
            >
              <!--  -->
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox
                  :label="cb"
                  v-for="(cb, i) in item.attr_vals"
                  :key="i"
                  border
                ></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item
              :label="item.attr_name"
              v-for="item in onlyTableData"
              :key="item.attr_id"
            >
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <!-- action: API -->
            <el-upload
              :action="uploadURL"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :headers="headerObj"
              list-type="picture"
              :on-success="handleSuccess"
            >
              <el-button size="small" type="primary"></el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <!--  -->
            <quill-editor v-model="addForm.goods_introduce"></quill-editor>
            <!--  -->
            <el-button type="primary" class="btnAdd" @click="addGoods"
              ></el-button
            >
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
    <el-dialog
      title=""
      :visible.sync="previewDialogVisible"
      width="50%"
    >
      <img :src="picPreviewPath" alt="" class="previewImg" />
    </el-dialog>
  </div>
</template>

<script>
import _ from "lodash";
import bread from "@/components/bread";
export default {
  components: {
    bread
  },
  data() {
    return {
      dataPath: [{ name: "" }, { name: "" }],
      //  Tab
      activeIndex: "0",
      // 
      addForm: {
        goods_name: "",
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
        // 
        goods_cat: [],
        // 
        pics: [],
        // 
        goods_introduce: "",
        attrs: []
      },
      addFormRules: {
        goods_name: [
          { required: true, message: "", trigger: "blur" }
        ],
        goods_price: [
          { required: true, message: "", trigger: "blur" }
        ],
        goods_weight: [
          { required: true, message: "", trigger: "blur" }
        ],
        goods_number: [
          { required: true, message: "", trigger: "blur" }
        ],
        goods_cat: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      // 
      cateList: [],
      // 
      cascaderProps: {
        expandTrigger: "hover",
        label: "cat_name",
        value: "cat_id",
        children: "children"
      },
      // 
      manyTableData: [],
      // 
      onlyTableData: [],
      // 
      uploadURL: "http://127.0.0.1:8888/api/private/v1/upload",
      // 
      headerObj: {
        Authorization: window.sessionStorage.getItem("token")
      },
      picPreviewPath: "",
      // 
      previewDialogVisible: false
    };
  },
  created() {
    this.getCateList();
  },
  computed: {
    getCateId() {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2];
      }
      return null;
    }
  },
  methods: {
    // 
    async getCateList() {
      const { data: res } = await this.$http.get("categories");
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.cateList = res.data;
    },
    // 
    handleChange() {
      if (this.addForm.goods_cat.length !== 3) {
        this.addForm.goods_cat = [];
      }
    },
    beforeTabLeave(activeName, odlActiveName) {
      // Tab
      if (odlActiveName === "0" && this.addForm.goods_cat.length !== 3) {
        this.$message.error("");
        return false;
      }
    },
    // Tab
    async tabClicked() {
      // 
      if (this.activeIndex === "1") {
        const { data: res } = await this.$http.get(
          `categories/${this.getCateId}/attributes`,
          {
            params: { sel: "many" }
          }
        );
        if (res.meta.status !== 200) {
          return this.$message.error("");
        }
        res.data.forEach(item => {
          item.attr_vals =
            item.attr_vals.length === 0 ? [] : item.attr_vals.split(" ");
        });
        this.manyTableData = res.data;
      } else if (this.activeIndex === "2") {
        const { data: res } = await this.$http.get(
          `categories/${this.getCateId}/attributes`,
          {
            params: { sel: "only" }
          }
        );
        if (res.meta.status !== 200) {
          return this.$message.error("");
        }
        this.onlyTableData = res.data;
      }
    },
    // 
    handlePreview(file) {
      this.picPreviewPath = file.response.data.url;
      this.previewDialogVisible = true;
    },
    // 
    handleRemove(file) {
      // 1.
      const filePath = file.response.data.tmp_path;
      // 2.pics
      const i = this.addForm.pics.findIndex(x => x.pic === filePath);
      // 3.splice
      this.addForm.splice(i, 1);
    },
    // 
    handleSuccess(response) {
      // 1. 
      const picInfo = { pic: response.data.tmp_path };
      // 2.pushpics
      this.addForm.pics.push(picInfo);
    },
    // 
    addGoods() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return this.$message.error("");
        // goods_cat attrs
        // this.addForm.goods_cat = this.addForm.goods_cat.join(',')
        // goods_cat
        // : lodash cloneDeep(boj)

        // this.addForm
        const form = _.cloneDeep(this.addForm);
        form.goods_cat = form.goods_cat.join(",");

        // 
        this.manyTableData.forEach(item => {
          const newInfo = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals.join(" ")
          };
          this.addForm.attrs.push(newInfo);
        });
        // 
        this.onlyTableData.forEach(item => {
          const newInfo = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals
          };
          this.addForm.attrs.push(newInfo);
        });
        form.attrs = this.addForm.attrs;
        // 
        // 
        const { data: res } = await this.$http.post("goods", form);
        if (res.meta.status !== 201)
          return this.$message.error("");
        this.$message.success("!");
        this.$router.push("/goods");
      });
    }
  }
};
</script>

<style lang="less" scoped>
.el-checkbox {
  margin: 0 8px 0 0 !important;
}
.previewImg {
  width: 100%;
}
.btnAdd {
  margin-top: 15px;
}
</style>



