<template>
  <div>
    <!---->
    <bread :fromComponentbread="dataPath"></bread>
    <!---->
    <el-card>
      <!---->
      <el-row :gutter="20">
        <el-col :span="12">
          <el-input
            placeholder=""
            v-model="queryinfo.query"
            clearable
            @clear="getuserList()"
          >
          </el-input>
        </el-col>
        <el-col :span="6">
          <el-button type="info" round @click="getuserList()"> </el-button>
        </el-col>
        <el-col :span="6">
          <el-button type="primary" class="adduserbtn" @click="isdialog = true">
            
          </el-button>
        </el-col>
      </el-row>
      <!---->
      <el-table border style="width: 100%" :data="userListdata">
        <el-table-column type="index" label=""> </el-table-column>
        <el-table-column prop="username" label=""> </el-table-column>
        <el-table-column prop="email" label=""> </el-table-column>
        <el-table-column prop="mobile" label=""> </el-table-column>
        <el-table-column prop="role_name" label=""> </el-table-column>
        <el-table-column prop="mg_state" label="">
          <template slot-scope="scope">
            <el-switch
              style="display: block"
              v-model="scope.row.mg_state"
              active-color="#13ce66"
              inactive-color="#ff4949"
              @change="userstatechange(scope.row)"
            >
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="" width="190px">
          <template slot-scope="scope">
            <el-tooltip
              effect="light"
              content=""
              placement="top-start"
              :enterable="false"
            >
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="showEditdialog(scope.row.id)"
              >
              </el-button>
            </el-tooltip>
            <el-tooltip
              effect="light"
              content=""
              placement="top-start"
              :enterable="false"
            >
              <el-button
                type="warning"
                icon="el-icon-delete"
                size="mini"
                @click="showDeletetip(scope.row.id)"
              >
              </el-button>
            </el-tooltip>
            <el-tooltip
              effect="light"
              content=""
              placement="top-start"
              :enterable="false"
            >
              <el-button
                type="danger"
                icon="el-icon-user"
                size="mini"
                @click="showSetRole(scope.row)"
              >
              </el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!---->
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page="queryinfo.pagenum"
        :page-size="queryinfo.pagesize"
        layout="total, prev, pager, next"
        :total="total"
      >
      </el-pagination>
      <!---->
      <el-dialog
        title=""
        :visible.sync="isdialog"
        width="50%"
        center
        @close="addDialogClose()"
      >
        <span>
          <el-form
            ref="addformRef"
            :model="addform"
            label-width="70px"
            :rules="addrules"
          >
            <el-form-item label="" prop="username">
              <el-input v-model="addform.username"></el-input>
            </el-form-item>
            <el-form-item label="" prop="password">
              <el-input v-model="addform.password"></el-input>
            </el-form-item>
            <el-form-item label="" prop="email">
              <el-input v-model="addform.email"></el-input>
            </el-form-item>
            <el-form-item label="" prop="mobile">
              <el-input v-model="addform.mobile"></el-input>
            </el-form-item>
          </el-form>
        </span>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="adduser()"> </el-button>
          <el-button @click="isdialog = false">
             
          </el-button>
        </span>
      </el-dialog>
      <!---->
      <el-dialog
        title=""
        :visible.sync="editIsdialog"
        width="50%"
        center
        @close="editDialogClose()"
      >
        <span>
          <el-form
            ref="editformRef"
            :model="editform"
            label-width="70px"
            :rules="editrules"
          >
            <el-form-item label="" prop="username">
              <el-input v-model="editform.username" disabled></el-input>
            </el-form-item>
            <el-form-item label="" prop="email" type="email">
              <el-input v-model="editform.email"></el-input>
            </el-form-item>
            <el-form-item label="" prop="mobile">
              <el-input v-model="editform.mobile"></el-input>
            </el-form-item>
          </el-form>
        </span>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="edituser()">
             
          </el-button>
          <el-button @click="editIsdialog = false">
             
          </el-button>
        </span>
      </el-dialog>
      <!---->
      <el-dialog
        title=""
        :visible.sync="setRoleDialogVisible"
        width="50%"
        @close="setRoleDialogClosed"
      >
        <div>
          <p>{{ userInfo.username }}</p>
          <p>{{ userInfo.role_name }}</p>
          <p>
            
            <el-select
              v-model="selectRoleId"
              filterable
              allow-create
              default-first-option
              placeholder=""
            >
              <el-option
                v-for="item in rolesLsit"
                :key="item.id"
                :label="item.roleName"
                :value="item.id"
              ></el-option>
            </el-select>
          </p>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="setRoleDialogVisible = false"> </el-button>
          <el-button type="primary" @click="saveRoleInfo"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import { isEmail } from "@/utils/validation";
import bread from "@/components/bread";
export default {
  components: {
    bread
  },
  data() {
    //
    let validateEmail = (rule, value, callback) => {
      if (!isEmail(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    //
    let checkPhone = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(""));
      } else {
        const reg = /^1[3|4|5|7|8][0-9]\d{8}$/;
        console.log(reg.test(value));
        if (reg.test(value)) {
          callback();
        } else {
          return callback(new Error(""));
        }
      }
    };
    return {
      isdialog: false,
      dataPath: [{ name: "" }, { name: "" }],
      //
      addform: {
        username: "",
        password: ""
      },
      //
      queryinfo: {
        query: "",
        pagenum: 1,
        pagesize: 3
      },
      //
      userListdata: [],
      //
      total: 0,
      //
      isdialog: false,
      //
      editIsdialog: false,
      //
      addrules: {
        username: [
          { required: true, message: "", trigger: "blur" },
          { min: 5, max: 10, message: " 5  10 ", trigger: "blur" }
        ],
        password: [
          { required: true, message: "", trigger: "blur" },
          { min: 6, max: 12, message: " 6  12 ", trigger: "blur" }
        ],
        email: [
          { required: true, message: "", trigger: "blur" },
          { validator: validateEmail, trigger: "blur" }
        ],
        mobile: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkPhone, trigger: "blur" }
        ]
      },
      editform: {},
      editrules: {
        email: [
          { required: true, message: "", trigger: "blur" },
          { validator: validateEmail, trigger: "blur" }
        ],
        mobile: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkPhone, trigger: "blur" }
        ]
      },
      setRoleDialogVisible: false,
      // 
      userInfo: {},
      // 
      rolesLsit: [],
      // Id
      selectRoleId: ""
    };
  },
  created() {
    this.getuserList();
  },
  methods: {
    //
    async getuserList() {
      const res = await this.$http.get("users", { params: this.queryinfo });
      if (res.data.meta.status !== 200) {
        return this.$message.error("");
      } else {
        this.total = res.data.data.total;
        this.queryinfo.pagenum = res.data.data.pagenum;
        this.userListdata = res.data.data.users;
      }
    },
    //
    handleCurrentChange(newpage) {
      this.queryinfo.pagenum = newpage;
      this.getuserList();
    },
    //
    async userstatechange(newstate) {
      const res = await this.$http.put(
        `users/${newstate.id}/state/${newstate.mg_state}`
      );
      if (res.data.meta.status !== 200) {
        newstate.mg_state = !newstate.mg_state;
        return this.$message.error("");
      } else {
        return this.$message.success(res.data.meta.msg);
      }
    },
    //
    addDialogClose() {
      this.$refs.addformRef.resetFields();
    },
    //
    adduser() {
      this.$refs.addformRef.validate(async valid => {
        if (!valid) {
          return;
        } else {
          const res = await this.$http.post("users", this.addform);
          console.log(res);
          if (res.data.meta.status !== 201) {
            return this.$message.error(",");
          } else {
            this.isdialog = false;
            this.$message.success("");
            this.getuserList();
          }
        }
      });
    },
    //
    async showEditdialog(id) {
      const res = await this.$http.get(`users/${id}`);
      console.log(res);
      this.editform = res.data.data;
      this.editIsdialog = true;
    },
    //
    editDialogClose() {
      this.$refs.editformRef.resetFields();
    },
    //
    edituser() {
      this.$refs.editformRef.validate(async valid => {
        if (!valid) {
          return;
        } else {
          const res = await this.$http.put(`users/${this.editform.id}`, {
            email: this.editform.email,
            mobile: this.editform.mobile
          });
          console.log(res);
          if (res.data.meta.status !== 200) {
            return this.$message.error("");
          } else {
            this.editIsdialog = false;
            this.$message.success("");
            this.getuserList();
          }
        }
      });
    },
    //
    async showDeletetip(id) {
      const confirmresult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).catch(err => err);
      if (confirmresult == "confirm") {
        const res = await this.$http.delete(`users/${id}`);
        if (res.data.meta.status !== 200) {
          this.$message.error("");
        } else {
          this.$message.success("");
          this.getuserList();
        }
      } else {
        return;
      }
    },
    // 
    async showSetRole(role) {
      this.userInfo = role;
      // 
      const { data: res } = await this.$http.get("roles");
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.rolesLsit = res.data;
      this.setRoleDialogVisible = true;
    },
    // 
    async saveRoleInfo() {
      if (!this.selectRoleId) {
        return this.$message.error("");
      }
      const { data: res } = await this.$http.put(
        `users/${this.userInfo.id}/role`,
        {
          rid: this.selectRoleId
        }
      );
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.$message.success("");
      this.getuserList();
      this.setRoleDialogVisible = false;
    },
    // 
    setRoleDialogClosed() {
      this.selectRoleId = "";
      this.userInfo = {};
    }
  }
};
</script>

<style lang="less" scoped>
.el-breadcrumb {
  margin-bottom: 20px;
}
.el-table {
  margin-top: 20px;
  font-size: 14px;
}
.el-switch {
  height: 25px;
}
.adduserbtn {
  position: absolute;
  right: 10px;
}
</style>



